<template>
  <div class="flex-1 h-0 flex-column">
    <v-content-table>
      <template v-for="(item, index) in jsonData.table || []">
        <el-table-column :label="item.label" :prop="item.prop"></el-table-column>
      </template>
    </v-content-table>
    <div class="flex-1 h-0" style="overflow-y: auto">
      <div>
        <el-button type="primary" size="mini" @click="parseJsonMount">update</el-button>
        <el-button type="primary" size="mini" @click="createJavascript">生成文件内容</el-button>
      </div>
      <table-column v-model="jsonMount" :delete-show="false" disable @delete="deleteAction"></table-column>
    </div>
    <el-dialog :visible="visible" show-close @close="()=>visible=false">
      <pre>
        {{ html }}
      </pre>
    </el-dialog>
  </div>
</template>

<script>
import VContentTable from "@/components/table/table";
import TableArray from "@/components/table/tableArray";
import TableObject from "@/components/table/tableObject";

export default {
  name: "vue-table",
  components: {TableObject, TableArray, VContentTable},
  data() {
    return {
      jsonMount: {
        key: 'table-config',
        type: 'object',
        value: {},
        properties: {}
      },
      jsonData: {},
      html: '',
      visible: false
    }
  },
  computed: {},
  mounted() {

  },
  watch: {
    jsonMount: {
      handler(val) {
        this.parseJsonMount(val);
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    deleteAction({key}) {
      delete this.jsonMount.properties[key]
    },
    parseJsonMount() {
      this.jsonData = this.parseObject(this.jsonMount);
    },
    parseObject(object) {
      const map = {}
      const list = Object.keys(object.properties);
      if (list.length === 0) {
        return map;
      }
      const _that = this;
      list.forEach(item => {
        const value = object.properties[item];
        map[item] = _that.parseProperties(value);
      })
      return map;
    },
    parseArray(object) {
      const resultList = [];
      const list = Object.keys(object.properties);
      if (list.length === 0) {
        return resultList;
      }
      const _that = this;
      list.forEach(item => {
        const value = object.properties[item];
        resultList.push(_that.parseProperties(value))
      })
      return resultList;
    },
    parseProperties(value) {
      if (value.type === 'string' || value.type === 'null' || value.type === 'boolean') {
        return value.value;
      } else if (value.type === 'object') {
        return this.parseObject(value);
      } else if (value.type === 'array') {
        return this.parseArray(value)
      }
    },
    createJavascript() {
      this.parseJsonMount();
      const template = `<div class="flex-1 h-0 flex-column " style="background-color: #ffffff">
<v-content-table class="flex-1 h-0">
        ${this.createTableColumn()}
        <template slot="header">
        <el-row>
        <el-form>
        ${this.createTableHeader()}
</el-form>
</el-row>
</template>
</v-content-table>
</div>`
      this.visible = true;
      this.html = template;
    },
    createTableColumn() {
      let content = '';
      const length = this.jsonData.table && this.jsonData.table.length || 0;
      for (let i = 0; i < length; i++) {
        const item = this.jsonData.table[i];
        content += `<el-table-column ${item.label ? `label="${item.label}"` : ''} ${item.prop ? `prop=${item.prop}` : ''}></el-table-column>`
      }
      return content;
    },
    createTableHeader() {
      let content = '';
      const length = this.jsonData.header && this.jsonData.header.length || 0;
      for (let i = 0; i < length; i++) {
        const item = this.jsonData.header[i];
        content += `<el-col :span="6">
            <el-form-item label="${item.label}">
            <el-input v-model="config.${item.prop}"></el-input>
</el-form-item>
</el-col>`
      }
      return content;
    }
  }
}
</script>

<style scoped>
/deep/ .el-form-item {
  display: flex;
  flex-direction: column;
}
</style>